import './SuccessPopup.css';
import success_img from '../../images/success.png';

function SuccessPopup(props) {


    return (
        <div className={props.successPopup ? 'success-popup success-popup_opened' : 'success-popup'}>
            <div className='success-popup__wrap'>
                <button className="success-popup__close-button" onClick={() => props.setSuccessPopup(false)}>X</button>
                <img src={success_img} className='success-popup__img' alt="Картинка зеленой галочки"></img>
                <div className='success-popup__text'>
                    <p className='text success-popup__title'>Сообщение отправленно успешно.</p>
                    <p className='text success-popup__subtitle'>Мы скоро свяжемся с Вами</p>
                </div>
            </div>
        </div>
    )
}

export default SuccessPopup;