import About from '../About/About';
import Contacts from '../Contacts/Contacts';
import SimpleSlider from '../Slider/Slider';
import './Main.css';

function Main(props) {
  return (
    <div className="main">
        <SimpleSlider />
        <About />
        <Contacts 
          setPopupOpen={props.setPopupOpen}
          setSuccessPopupOpen={props.setSuccessPopupOpen}
          successPopup={props.successPopup}
        />
    </div>
  );
}

export default Main;