import emailjs from 'emailjs-com';
import { useEffect, useRef, useState } from 'react';
import './Form.css';


function Form(props) {
    //хуки для валидации формы
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [message, setMessage] = useState('');
    //были ли мы внутри инпута или нет
    const [nameDirty, setNameDirty] = useState(false);
    const [emailDirty, setEmailDirty] = useState(false);
    const [phoneDirty, setPhoneDirty] = useState(false);
    const [messageDirty, setMessageDirty] = useState(false);
    //состояние, которое отражает ошибку
    const [nameError, setNameError] = useState('Поле "Имя" не может быть пустым');
    const [emailError, setEmailError] = useState('Поле "e-mail" не может быть пустым');
    const [phoneError, setPhoneError] = useState('Поле "Телефон" не может быть пустым');
    const [messageError, setMessageError] = useState('Поле "Сообщение" не может быть пустым');

    const [formValid, setFormValid] = useState(false)

    //
    useEffect(() => {
        if(nameError || emailError || phoneError || messageError) {
            setFormValid(false)
        } else {
            setFormValid(true)
        }
    }, [nameError, emailError, phoneError, messageError])

    //функция проверки, если пользователь убрал курсор из поля импута
    const blurHandler = (evt) => {
        // eslint-disable-next-line default-case
        switch(evt.target.name) {
            case 'name':
                setNameDirty(true)
                break
            case 'phone': 
                setPhoneDirty(true)
                break
            case 'email':
                setEmailDirty(true)
                break
            case 'text': 
                setMessageDirty(true)
                break
        }
    }

    //логика проверки всех инпутов на валидность
    const nameInputHandler = (evt) => {
        setName(evt.target.value);

        let re = /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{1,50}$/;
        if(re.test(String(evt.target.value))) {
            setNameError("Нельзя использовать цифры в имени");
        } else {
            setNameError("")
        }
    }

    const phoneInputHandler = (evt) => {
        setPhone(evt.target.value);

        let re = /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/;
        if(!re.test(String(evt.target.value))) {
            setPhoneError("Неккоректный номер телефона");
        } else {
            setPhoneError("")
        }
    }

    const emailInputHandler = (evt) => {
        setEmail(evt.target.value);

        let re = /^[a-z][a-zA-Z0-9_.]*(\.[a-zA-Z][a-zA-Z0-9_.]*)?@[a-z][a-zA-Z-0-9]*\.[a-z]+(\.[a-z]+)?$/;
        if(!re.test(String(evt.target.value))) {
            setEmailError("Неккоректный email");
        } else {
            setEmailError("");
        }
    }

    const messageInputHandler = (evt) => {
        setMessage(evt.target.value);

        if(evt.target.value.length < 5) {
            setMessageError("Неккоректное сообщение");
            if(!evt.target.value) {
                setMessageError("Поле 'Сообщение' не может быть пустым");
            }
        } else {
            setMessageError();
        }
    }

    //логика отправки формы на емайл
    const form = useRef();

    function sendEmail(evt) {
        evt.preventDefault();

        emailjs.sendForm('service_6tiacnd', 'gmail_test', evt.target, 'user_Vm7np7NcwHFtKyMUjjoPR')
          .then((result) => {
              console.log(result.text);
              console.log("письмо отправлено");
              props.setSuccessPopupOpen(true);
          }, (error) => {
              console.log(error.text);
        });
        evt.target.reset();
    }

    return (
        <form className="form" ref={form} onSubmit={sendEmail}>
            <label>
                {(nameDirty && nameError) && <span className='error'>{nameError}</span>}
                <input 
                    onChange={(evt) => nameInputHandler(evt)}
                    onBlur={(evt) => blurHandler(evt)}
                    className="form-input"
                    placeholder="Имя"
                    type="text"
                    name="name"
                    value={name}
                ></input>
            </label>
            <label>
                {(phoneDirty && phoneError) && <span className='error'>{phoneError}</span>}
                <input
                    onChange={(evt) => phoneInputHandler(evt)}
                    onBlur={(evt) => blurHandler(evt)}
                    className="form-input"
                    required
                    placeholder="Телефон"
                    type="tel"
                    name="phone"
                    value={phone}
                ></input>
                <span className='error'></span>
            </label>
            <label>
                {(emailDirty && emailError) && <span className='error'>{emailError}</span>}
                <input
                    onChange={(evt) => emailInputHandler(evt)}
                    onBlur={(evt) => blurHandler(evt)}
                    className="form-input"
                    placeholder="E-mail"
                    type="email"
                    name="email"
                    value={email}
                ></input>
                <span className='error'></span>
            </label>
            <label>
                {(messageDirty && messageError) && <span className='error'>{messageError}</span>}
                <textarea
                    onChange={(evt) => messageInputHandler(evt)}
                    onBlur={(evt) => blurHandler(evt)}
                    className="form-input form-input_text"
                    placeholder="Сообщение"
                    type="text"
                    name="text"
                    maxLength="1000"
                    value={message}
                ></textarea>
                <span className='error'></span>
            </label>
            <button disabled={!formValid} type="submit" className={formValid ? "form-submit-button" : "form-submit-button form-submit-button_disabled"}>Отправить</button> 
        </form>
    )
}

export default Form;